.sidebar{
    min-height: 100vh;
    display: inline-flex;
    flex-direction: column;
    justify-content:space-between;
    background-color: #f0f4f9;
    padding: 25px 15px;
}
.sidebar img {
    width: 20px;
}
.sidebar .hamburger{
    display: block;
    margin-left: 10px;
    cursor: pointer;
}
.sidebar .new-chat{
    margin-top: 50px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px;
    background-color: #e5eaf1;
    border-radius: 50px;
    color: #989ca0;
    cursor: pointer;
    font-size: 14px;
}
.sidebar .recent {
    display: flex;
    flex-direction: column;
}
.sidebar .recent-title{
    margin-top: 30px;
    margin-bottom: 20px;
}
.sidebar .recent-entry{
    display: flex;
    align-items: start;
    gap: 10px;
    padding: 10px;
    padding-right: 40px;
    border-radius: 50px;
    color: #282828;
    cursor: pointer;
}
.sidebar .recent-entry:hover{
    background-color: #e2e6eb;
}
.sidebar .bottom{
    display: flex;
    flex-direction: column;
}
.sidebar .bottom-item{
    padding-right: 10px;
}

@media screen and (max-width: 600px) {
    .sidebar{
        padding: 25px 10px;
    }   
    .sidebar .new-chat{
        padding: 5px 10px;
    }
    .main .cards ul li{
        height: 120px;
    }
    .search-box img{
        width: 20px;
    }
    .search-box div{
        gap: 7px;
    }
    .sidebar img{
        width: 18px;
    }
}